"use client";

import { cn } from "@/lib/cn";
import { formatDayLabelRu, formatWeekdayShortRu } from "@/lib/slots";

type Props = {
  days: string[];
  value: string;
  onChange: (iso: string) => void;
  hotDays?: Set<string>;     // red notch
  premiumDays?: Set<string>; // blue notch
};

export default function DayTabs({ days, value, onChange, hotDays, premiumDays }: Props) {
  return (
    <div className="rounded-2xl border border-zinc-200 bg-white p-3">
      <div className="flex gap-2 overflow-x-auto pb-1 [-webkit-overflow-scrolling:touch]">
        {days.map((iso) => {
          const active = iso === value;
          const isHot = hotDays?.has(iso);
          const isPremium = premiumDays?.has(iso);

          return (
            <button
              key={iso}
              onClick={() => onChange(iso)}
              className={cn(
                "relative min-w-[92px] flex-shrink-0 rounded-2xl border px-3 py-2 text-left transition",
                active
                  ? "border-zinc-900 bg-zinc-900 text-white"
                  : "border-zinc-200 bg-white text-zinc-800 hover:bg-zinc-50"
              )}
            >
              {/* notches */}
              <div className="absolute right-2 top-2 flex items-center gap-1">
                {isPremium ? (
                  <span
                    className={cn("h-2 w-2 rounded-full", active ? "bg-sky-300" : "bg-sky-500")}
                    aria-label="Высокий тариф"
                    title="Высокий тариф"
                  />
                ) : null}
                {isHot ? (
                  <span
                    className={cn("h-2 w-2 rounded-full", active ? "bg-red-300" : "bg-red-500")}
                    aria-label="Горящие слоты"
                    title="Горящие слоты"
                  />
                ) : null}
              </div>

              <div className={cn("text-xs", active ? "text-white/80" : "text-zinc-500")}>
                {formatWeekdayShortRu(iso)}
              </div>
              <div className="mt-0.5 text-sm font-semibold">{formatDayLabelRu(iso)}</div>
            </button>
          );
        })}
      </div>

      <div className="mt-2 flex flex-wrap gap-3 px-1 text-[11px] text-zinc-500">
        <div className="flex items-center gap-1">
          <span className="h-2 w-2 rounded-full bg-sky-500" /> высокий тариф
        </div>
        <div className="flex items-center gap-1">
          <span className="h-2 w-2 rounded-full bg-red-500" /> горящие слоты
        </div>
      </div>
    </div>
  );
}
